"use client"
import React, { useEffect, useRef, useState } from "react";
import "./Header.css";

const NAV_LINKS = [
  { href: "#what-we-actually-do", label: "Services" },
  { href: "#three-ways-to-start", label: "Contact" }
];

const Header = () => {
  const headerRef = useRef();
  const [mobileNavOpen, setMobileNavOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (headerRef.current) {
        if (window.scrollY > 60) {
          headerRef.current.classList.add("shrunk");
        } else {
          headerRef.current.classList.remove("shrunk");
        }
      }
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  useEffect(() => {
    if (!mobileNavOpen) return;
    const firstFocusable = document.querySelector('.mobile-nav a, .mobile-nav button');
    if (firstFocusable) firstFocusable.focus();
    const trap = (e) => {
      if (e.key === "Escape") setMobileNavOpen(false);
    };
    document.addEventListener("keydown", trap);
    return () => document.removeEventListener("keydown", trap);
  }, [mobileNavOpen]);

  return (
    <header className="header" ref={headerRef}>
      <div className="header-inner">
        {/* Logo left */}
        <div className="header-section header-left">
          <a href="/" aria-label="Home" className="logo-link">
            <svg
              className="logo-svg"
              viewBox="0 0 400 200"
              xmlns="http://www.w3.org/2000/svg"
              aria-label="2FIND Logo"
              focusable="false"
            >
              <rect width="400" height="200" fill="#FAFAFA"/>
              <g transform="translate(200, 100)">
                <circle cx="-40" cy="0" r="30" stroke="#1E3A8A" strokeWidth="3.5" fill="none"/>
                <circle cx="-15" cy="0" r="30" stroke="#10B981" strokeWidth="3.5" fill="none"/>
                <defs>
                  <linearGradient id="overlapGradient" x1="0%" y1="0%" x2="100%" y2="0%">
                    <stop offset="0%" style={{ stopColor: "#1E3A8A", stopOpacity: 0.08 }} />
                    <stop offset="50%" style={{ stopColor: "#10B981", stopOpacity: 0.12 }} />
                    <stop offset="100%" style={{ stopColor: "#10B981", stopOpacity: 0.08 }} />
                  </linearGradient>
                </defs>
                <ellipse cx="-27.5" cy="0" rx="15" ry="30" fill="url(#overlapGradient)"/>
                <text x="25" y="7" fontFamily="Inter, sans-serif" fontSize="36" fontWeight="300" fill="#1E3A8A">2</text>
                <text x="48" y="7" fontFamily="Inter, sans-serif" fontSize="36" fontWeight="700" fill="#1E3A8A">FIND</text>
              </g>
            </svg>
          </a>
        </div>

        {/* Desktop Nav right */}
        <nav className="header-section header-right main-nav" aria-label="Main navigation">
          <ul>
            {NAV_LINKS.map(link => (
              <li key={link.href}>
                <a href={link.href} tabIndex={0}>{link.label}</a>
              </li>
            ))}
          </ul>
        </nav>

        {/* Hamburger for mobile */}
        <div className="header-section mobile-hamburger">
          <button
            className="hamburger"
            aria-label={mobileNavOpen ? "Close navigation" : "Open navigation"}
            aria-expanded={mobileNavOpen}
            aria-controls="mobile-menu"
            onClick={() => setMobileNavOpen(!mobileNavOpen)}
            type="button"
          >
            <span className="hamburger-box" aria-hidden="true">
              <span className={`hamburger-inner${mobileNavOpen ? " open" : ""}`}></span>
            </span>
          </button>
        </div>
      </div>

      {/* Mobile Nav Drawer */}
      <nav
        id="mobile-menu"
        className={`mobile-nav${mobileNavOpen ? " open" : ""}`}
        aria-label="Mobile navigation"
      >
        <ul>
          {NAV_LINKS.map(link => (
            <li key={link.href}>
              <a
                href={link.href}
                tabIndex={mobileNavOpen ? 0 : -1}
                onClick={() => setMobileNavOpen(false)}
                style={{ minHeight: 44, minWidth: 44, display: "flex", alignItems: "center" }}
              >
                {link.label}
              </a>
            </li>
          ))}
        </ul>
      </nav>
      {/* Overlay for closing menu */}
      {mobileNavOpen && <div className="nav-overlay" tabIndex={-1} aria-hidden="true" onClick={() => setMobileNavOpen(false)} />}
    </header>
  );
};

export default Header;